<template>
  <div>
      <div class="container-md">
        <h1 class="text-center mt-5 mb-5">Flores</h1>
        <h2 class="text-center mb-5">Aquí tienes una buena selección de flores para principiantes. ¡Ánimo, seguro esta vez no se mueren!</h2>
          <b-card-group columns>
            <div columns v-for="proyecto in proyectos" :key="proyecto.id">
             <Card2 :autor="proyecto.autor" :srcimg="proyecto.srcimg" :alt="proyecto.alt" :titulo="proyecto.titulo" :texto="proyecto.texto" :url="proyecto.url"></Card2> 
            </div>
           </b-card-group>
       </div>
 </div>
</template>

<script>
import Card2 from '../components/MiCard.vue' 
const lstProysJson = '../https://raw.githubusercontent.com/lauravilasanchez/niflowers2/main/ElArchivo.json';
export default {
  props: {  
   name: {
      type: String,
      default: 'AcercaDe'
    }
  },
  components: {
        Card2
      },
  data() {
      return {
        proyectos:[],
        githubPage: 1,
        page: 1,
        loading: false,
        perPage: 5
      }
    },
    mounted() {
      this.fetchData()
    },
   
    methods: {
        async fetchData() {
          const res = await fetch (lstProysJson);
          const val= await res.json();
          this.proyectos=val;
          console.log(val);
        }
    }
  }
</script>
