import Vue from 'vue'
import Router from 'vue-router'
import ElInicio from '@/views/ElInicio.vue'
import AcercaDe from '@/views/AcercaDe.vue'
import ContactoDe from '@/views/ContactoDe.vue'
import OtraPag from '@/views/OtraPag.vue'

Vue.use(Router)

export default new Router({
  mode: "history",
  base: '/niflowers2/',
  routes: [
    {
      path: '/',
      name: 'ElInicio',
      component: ElInicio
    },
    {
      path: '/acerca-de',
      name: 'AcercaDe',
      component: AcercaDe
    },
    {
      path: '/contacto-de',
      name: 'ContactoDe',
      component: ContactoDe
    },
    {
      path: '/otra-pag',
      name: 'OtraPag',
      component: OtraPag
    },
  ]
})
